import React, { useEffect, useRef } from 'react';
import './Skills.css';

function Skills() {
  const skillsRef = useRef(null);

  const skills = [
    { name: 'Java', proficiency: '75%' },
    { name: 'Spring Boot', proficiency: '75%' },
    { name: 'Databases (PostgreSQL/MySQL)', proficiency: '70%' },
    { name: 'JavaScript', proficiency: '70%' },
    { name: 'React', proficiency: '65%' },
    { name: 'WebSockets', proficiency: '60%' },
    { name: 'OAuth 2.0 / SAML SSO', proficiency: '40%' },
    { name: 'Python', proficiency: '35%' },
  ];

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const skillBars = entry.target.querySelectorAll('.skill-progress');
            skillBars.forEach((bar, index) => {
              // Get percentage from the parent element
              const percentage = skills[index].proficiency;
              // Set custom property for animation target
              bar.style.setProperty('--skill-percentage', percentage);
              // Add staggered delay for each bar
              bar.style.animationDelay = `${index * 0.2}s`;
            });
            observer.unobserve(entry.target);
          }
        });
      },
      { threshold: 0.1 }
    );

    if (skillsRef.current) {
      observer.observe(skillsRef.current);
    }

    return () => {
      if (skillsRef.current) {
        observer.unobserve(skillsRef.current);
      }
    };
  }, [skills]);

  return (
    <section id="skills">
      <h2>Technical Skills</h2>
      <div className="skills-container" ref={skillsRef}>
        {skills.map((skill, index) => (
          <div key={index} className="skill">
            <span className="skill-name">{skill.name}</span>
            <div className="skill-bar">
              <div className="skill-progress"></div>
            </div>
            <span className="skill-percentage">{skill.proficiency}</span>
          </div>
        ))}
      </div>
    </section>
  );
}

export default Skills;
