import React, { useState } from 'react';
import { LinkedinOutlined, MailOutlined, PhoneOutlined } from '@ant-design/icons';
import { Button, Typography, Row, Col } from 'antd';

const { Title, Paragraph } = Typography;

const fontFamily = "'Poppins', 'Segoe UI', 'Roboto', sans-serif";

const Hero = () => {
  const [clickedItem, setClickedItem] = useState(null);

  const handleCvButtonClick = () => {
    setClickedItem('cv');
    // Reset the clicked state after animation completes
    setTimeout(() => {
      setClickedItem(null);
    }, 500);
  };

  return (
    <Row justify="center" align="middle" style={{ minHeight: '100vh', backgroundColor: '#f9fafb' }}>
      <Col xs={24} md={16} style={{ textAlign: 'center' }}>
        <Title level={1} style={{ color: '#1e293b', fontFamily }}>Gunapal P</Title> {/* Dark text for visibility */}
        <Title level={3} style={{ color: '#1e293b', fontFamily }}>Full Stack Java Developer</Title> {/* Dark text for visibility */}
        <Paragraph style={{ color: '#4b5563', fontSize: '18px', fontFamily }}>
          5+ years of experience building scalable enterprise applications with Java, Spring Boot, and ReactJS.
        </Paragraph>
        <div style={{ marginBottom: '24px' }}>
          <Button 
            type="link" 
            href="tel:+918050735082" 
            icon={<PhoneOutlined style={{ fontSize: '24px', transform: 'scaleX(-1)' }} />} 
            style={{ color: '#40a9ff', padding: '12px', fontFamily }} 
          />
          <Button type="link" href="https://linkedin.com/in/gunapal-p" icon={<LinkedinOutlined style={{ fontSize: '24px' }} />} style={{ color: '#40a9ff', padding: '12px', fontFamily }} />
          <Button type="link" href="mailto:gunapalpgp@gmail.com" icon={<MailOutlined style={{ fontSize: '24px' }} />} style={{ color: '#40a9ff', padding: '12px', fontFamily }} />
        </div>
        <Button 
          type="primary" 
          href="/Gunapal.pdf"
          download
          onClick={handleCvButtonClick}
          className={`cv-button ${clickedItem === 'cv' ? 'clicked' : ''}`}
          style={{ 
            borderRadius: '8px', 
            fontSize: '16px', 
            padding: '10px 24px',
            height: 'auto',
            fontFamily,
            transition: 'transform 0.3s ease',
            transform: clickedItem === 'cv' ? 'scale(0.95)' : 'scale(1)'
          }}
        >
          Download CV
        </Button>
      </Col>
    </Row>
  );
};

export default Hero;
